import React from 'react';

//import logo from './logo.svg';
import './App.css';
import './Semantic/components/button.css';
import './Semantic/components/header.css';
import'./Semantic/components/image.css';
import './Semantic/components/container.min.css';
import './Semantic/components/menu.min.css';

import TopBar from './components/TopBar';
import SideBar from './components/Sidebar';
import Content from './components/Content';
import FooterMenu from './components/FooterMenu';
import ContentMobile from './components/ContentMobile';
 
class App extends React.Component {
  constructor(props){
    super(props);
    this.state ={
      windowWidth:0,
      windowHeight:0
    };
    this.updateDimensions = this.updateDimensions.bind(this);
  }
  componentDidMount() {
    this.updateDimensions();
    window.addEventListener('resize', this.updateDimensions);
  }
  updateDimensions() {
    let windowWidth = typeof window !== 'undefined' ? window.innerWidth:0;
    let windowHeight = typeof window !=='undefined' ? window.innerHeight:0;

    this.setState({windowWidth,windowHeight});
  }

  render() {
    const { windowWidth }=this.state;
    const sidebarCollapsed = windowWidth < 1100;
    const styles ={
      white: (opacity = 1) => `rgba(255, 255, 255, ${opacity})`,
      black: (opacity = 1) => `rgba(165, 42, 42,  ${opacity})`,
      topBarHeight: 40,
      footerMenuHeight:50,

      showFooterMenuText: windowWidth > 500,
      showSidebar: windowWidth > 768,
      sidebarCollapsed,
      sidebarWidth: sidebarCollapsed ? 50 : 250
    };
   
    return(
      <div className="App" style={{
           backgroundColor:'282c34',
      }}>
          {styles.showSidebar && (
           <Content styles={styles}/>
        )} 
        
          {styles.showSidebar ?(
           <SideBar styles={styles}/>
        ) : (
          
          <TopBar styles={styles} />
        )}
          {!styles.showSidebar && (
           <ContentMobile styles={styles}/>)} 
         
         {!styles.showSidebar && (
          <FooterMenu  styles={styles} />
        )}
        
     </div>
    )
  }
}

export default App;
